	module.exports = {"data":{"wordpressWpLessons":{"id":"ac5c465b-9462-5cce-b21d-a223da4abb17","wordpress_id":3364,"title":"Document Your First Project","slug":"document-first-project","content":"<h2>Overview and Goals</h2>\n<p>It&#8217;s time to practice your newly developed project planning and prototyping skills! This first activity is a DHF favorite. You&#8217;ll be working through the design and planning process as you complete the Art Bot challenge.</p>\n<h2><strong><span style=\"color: #0091c9;\">Activity: Art Bots</span></strong></h2>\n<p>In this activity, you will be given a challenge to complete. This activity has three phases: planning, building, and reflecting/documenting. Follow each of these steps for this activity:</p>\n<p><em><strong>Challenge Prompt: Use the available materials to make a robot that draws.</strong></em></p>\n<h3>Materials</h3>\n<ol>\n<li>Electric toothbrushes + Batteries</li>\n<li>Paper cups</li>\n<li>Rubber bands</li>\n<li>Googly eyes</li>\n<li>Masking tape</li>\n<li>Markers</li>\n</ol>\n<h3>Project Steps</h3>\n<ol>\n<li>Using your Maker Notebook, write down your ideas, draw a sketch, or explain your plan.</li>\n<li>Use the available materials to create your design, object, or solution.</li>\n<li>Make your design!</li>\n<li>When your design / object / solution is complete, be prepared to answer the following questions:\n<ul>\n<li style=\"list-style-type: none;\">\n<ul>\n<li>What was the prompt or challenge?</li>\n<li>What is your creation?</li>\n<li>What was your initial idea or design?</li>\n<li>How did you create your solution? What materials did you use?</li>\n<li>Did you have to change anything from your initial idea to make it work?</li>\n<li>What would you do differently if you had to do this again?</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>An instructor will talk with you about your project and ask you these questions.</li>\n<li>Find a safe place for your Art Bot, you will be using these for another activity next session.</li>\n</ol>\n<p>&nbsp;</p>\n"}},"pathContext":{}}

